<template>
  <div class="fileUpload" style="width: 31.25rem;margin: 0 auto;">
    <input type="file" @change="fileUpload()" ref="fileInt" style="display:none;" />
    <el-button type="primary" @click="openFile()">
      上传
      <i class="el-icon-upload el-icon--right"></i>
    </el-button>
    <el-progress
      class="progress"
      :text-inside="true"
      :stroke-width="24"
      :percentage="complete"
      status="success"
    ></el-progress>
  </div>
</template>

<script>
export default {
  name: "FileUpload",
  data() {
    return {
      complete: 0,
    };
  },
  created() {},
  methods: {
    fileUpload() {
      if (this.$refs.fileInt.files[0] == null) {
        return;
      }
      const file = this.$refs.fileInt.files[0];
      let data = new FormData();
      data.append("file", file);

      this.$axios
        .post("/file/upload", data, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
          // transformRequest: [function(data) {
          // 	return data
          // }],
          onUploadProgress: (progressEvent) => {
            this.complete =
              ((progressEvent.loaded / progressEvent.total) * 100) | 0;
          },
        })
        .then((result) => {
          if (result.data.code == 200) {
            console.log("地址：");
            console.log(result.data);
          }
        });
    },
    openFile() {
      this.$refs.fileInt.click();
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.fileUpload {
  display: flex;
  justify-content: center;
  align-items: center;
}
.progress {
  margin-left: 1rem;
  width: 100%;
}
</style>
